import React, { useRef } from "react";
import { useFrame } from "@react-three/fiber";
import Cloud from "./Cloud";
import { space } from "../utils";
const Clouds = ({ args = [500, 500], ...props }) => {
  const cloudsRef = useRef();
  useFrame(({ clock }, delta) => {});
  return (
    <group {...props}>
      {Array(25)
        .fill(null)
        .map((_, index) => (
          <Cloud
            key={index}
            args={[100,100,32]}
            position={[Math.random() * 800 - 400,space(45),Math.random() *  500 - 450]}
            // position={[
            //   Math.random() * 800 - 400,
            //   500,
            //   Math.random() * 500 - 450,
            // ]}
            rotation={[space(-0.65), space(0), Math.random() * 360]}
          />
        ))}
    </group>
  );
};
export default Clouds;
