import React, { useRef, useMemo } from "react";
import {
  DoubleSide,
  RepeatWrapping,
  PlaneGeometry,
  SphereGeometry,
  Vector3,
  Clock,
} from "three";
import { useLoader, useFrame, useThree, extend } from "@react-three/fiber";
import { TextureLoader } from "three/src/loaders/TextureLoader";
import { Water } from "three/examples/jsm/objects/Water";
import { FormatTexture } from "../hooks";

extend({ Water });
const OceanPlane = ({
  wave: { height = 8, swell = 9.18, speed = 0.0009, offSetTime = 3 },
  args = [200, 200, 90, 90],
  sunColor,
  waterColor,
  alpha,
  heightMapSrc,
  AO,
  bumpScale,
  smokeScale,
  textureMapSrc,
  normalMapSrc,
  metallicMapSrc,
  edgeMapSrc,
  map = "/assets/img/normal.jpg",
  ...props
}) => {
  const heightMap = FormatTexture(heightMapSrc);
  const AoTextureMap = FormatTexture(AO);
  const textureMap = FormatTexture(textureMapSrc);
  const normalMap = FormatTexture(normalMapSrc);
  const metallicMap = FormatTexture(metallicMapSrc);
  const edgeMap = FormatTexture(edgeMapSrc);
  const ref = useRef();

  const wavesBuffer = (waveSize, magnitude) => {
    const theTime = performance.now() * speed;
    var pos = ref.current.geometry.attributes.position;

    let center = new Vector3(0, 0, 0);
    var vec3 = new Vector3(); // re-use
    
    for (var i = 0, l = pos.count; i < l; i++) {
      vec3.fromBufferAttribute(pos, i);
      vec3.sub(center);

      var z = Math.sin(vec3.length() / -waveSize + theTime) * magnitude;

      pos.setZ(i, z);
    }

    pos.needsUpdate = true;
  };
  useFrame(({ clock }, delta) => {
    if (ref && ref?.current) {
      wavesBuffer(height, swell);
      // ref.current.material.uniforms.time.value += delta/offSetTime
    }
  });
  return (
      <mesh ref={ref} transparent={true} {...props}>
        <planeGeometry
          attach="geometry"
          args={args}
          receiveShadow
          transparent={true}
        />
        <meshPhongMaterial
          attach="material"
          bumpScale={0}
          map={textureMap}
          aoMap={AoTextureMap}
          bumpMap={heightMap}
          lightMap={edgeMap}
          metalnessMap={metallicMap}
          normalMap={normalMap}
          // Feed the shaders as strings
          side={DoubleSide}
        />
      </mesh>
  );
};

export default OceanPlane;
