import React, { useRef, useMemo } from "react";
import { DoubleSide, RepeatWrapping, PlaneGeometry,SphereGeometry, Vector3, Clock } from "three";
import { useLoader, useFrame, useThree, extend } from "@react-three/fiber";
import { TextureLoader } from 'three/src/loaders/TextureLoader'
import { Water } from "three/examples/jsm/objects/Water";

extend({ Water })
const Ocean = ({
  wave: {
    height = 8,
    swell = 9.18,
    speed = 0.0009,
    offSetTime=3
  },
  args = [200, 200, 90, 90],
  sunColor,
  waterColor,
  alpha,
  map = '/assets/img/normal.jpg',
  ...props
}) => {
  const [colorMap] = useLoader(TextureLoader, [map])
  const ref = useRef()
  const gl = useThree((state) => state.gl)
  colorMap.wrapS = colorMap.wrapT = RepeatWrapping
  colorMap.repeat.set(500, 500);
  const geom = useMemo(() => new PlaneGeometry(...args), [])
  const config = useMemo(
    () => ({
      textureWidth: 1400,
      textureHeight: 1400,
      waterNormals: colorMap,
      sunDirection: new Vector3(1.1,0.0,0.1),
      eye:new Vector3(1.1,0.0,0.1),
      sunColor: sunColor,
      waterColor: waterColor,
      alpha:alpha|| .95,
      distortionScale: 5,
      side: DoubleSide,
      fog: true,
      format: gl.encoding
    }),
    [colorMap, gl.encoding, sunColor, waterColor,alpha]
  )
  const wavesBuffer = ( waveSize, magnitude )=>{

    const theTime =  performance.now() * speed;
    var pos = ref.current.geometry.attributes.position;
  
    let center = new Vector3(0,0,0);
    var vec3 = new Vector3(); // re-use
    for ( var i = 0, l = pos.count; i < l; i ++ ) {
  
      vec3.fromBufferAttribute(pos, i);
      vec3.sub(center);
      
      var z = Math.sin( vec3.length() /- waveSize + (theTime)) * magnitude;
      
      pos.setZ(i, z);
  
    }
  
    pos.needsUpdate = true
  
  }
  useFrame(({ clock }, delta) => {
    if (ref && ref?.current) {
      wavesBuffer(height,swell)
      ref.current.material.uniforms.time.value += delta/offSetTime
    }
  });
  return colorMap && <>
    <water ref={ref} args={[geom, config]} {...props} />
  </>

};

export default Ocean;
