import { Sky } from "@react-three/drei";
import { Vector3 } from "three";
import { space } from "../utils";
import { useSpring, animated } from "@react-spring/three";
// import { useSnapshot } from "valtio";
// import state from "../store";
import { easing } from "maath";
import { useRef, useState } from "react";
import { useFrame } from "@react-three/fiber";

export default function SunRise({
  inclination = 0.2,
  azimuth = 0,
  mieCoefficient = 0.82,
  mieDirectionalG = 0.59,
  rayleigh = 0,
  turbidity = 5,
}) {
  const [snap, setSnap] = useState({
    lightsOn: false,
  });
  const sunRef = useRef();
  // const snap = useSnapshot(state);
  const springs = useSpring({
    position: snap.lightsOn ? [0, space(0), space(0)] : [0, space(0), 0],
    opacity: snap.lightsOn ? 0 : 0.2,
  });
  let targetPosition = !snap.lightsOn
    ? new Vector3(0, 0.005, -0.1)
    : new Vector3(0, 0.5, -90);
  useFrame((state, delta) => {
    if (!!sunRef.current) {
      easing.damp3(
        sunRef.current.material.uniforms.sunPosition,
        targetPosition,
        0.25,
        delta
      );
    }
  });
  return (
    <animated.group position={springs.position}>
      <Sky
        ref={sunRef}
        distance={window.innerWidth}
        sunPosition={targetPosition}
        inclination={inclination}
        azimuth={azimuth}
        mieCoefficient={mieCoefficient}
        mieDirectionalG={mieDirectionalG}
        rayleigh={rayleigh}
        turbidity={turbidity}
      />
    </animated.group>
  );
}
