import React, { useState,useRef } from "react";
import { useSpring, animated } from "@react-spring/three";
import {
  Sparkles,
} from "@react-three/drei";
import { space } from "../utils";
import ObjectImproter from "../three/ObjectImporter";
import * as THREE from "three";

import { useScroll as useScroller } from "../hooks";
const Island = () => {
  const [stateSize, setSize] = useState({
    speed: 0.095,
    maxDistance: 900,
    resetDistance: 500,
    active: false,
    width: window.innerWidth,
    height: window.innerHeight,
    loaded: true,
  });
  const vec3 = (num) => Array(3).fill(num);
  const springs = useSpring({
    scale: stateSize.active ? 1.5 : 1,
    lightEmission: stateSize.active ? 1.5 : 0,
    fireFlies: !stateSize.active
      ? [-space(0.5), 0, space(1)]
      : [-space(0.5), -space(10), space(1)],
  });
  
  const counter = useRef({
    increaser: 0,
    limit:2,
    triger:1
  });
  useScroller((e) => {
    counter?.current && e.wheelDelta > 0
      ? counter.current.increaser ++
      : counter.current.increaser --
      if(counter.current.increaser > counter.current.limit )counter.current.increaser = counter.current.limit ;
      if(counter.current.increaser < -counter.current.limit )counter.current.increaser = -counter.current.limit ;
      if(counter.current.increaser > counter.current.triger && !stateSize.active){
        setSize((s) => ({ ...s, active: !s.active }));
      }
      if(counter.current.increaser < -counter.current.triger && stateSize.active){
        setSize((s) => ({ ...s, active: !s.active }));
      }
  });
  return (
    <group>
      <group
        name="house on the lake"
        position={[space(0), -space(0.1), space(0)]}
      >
        <animated.group position={springs.fireFlies}>
          <Sparkles
            count={500}
            scale={[30, 10, 20]}
            size={5}
            speed={Math.sin(performance.now() * (200 * Math.random()) - 1.0)}
          />
        </animated.group>
        <animated.group
          name="lamp_light"
          onClick={() => {
            setSize((s) => ({ ...s, active: !s.active }));
          }}
        >
          <animated.pointLight
            intensity={springs.lightEmission}
            position={[-space(2.4), space(3), space(4.73)]}
            color="#FFB76B"
            castShadow
          />
          <mesh position={[-space(2.4), space(3), space(4.73)]}>
            <boxGeometry
              attach="geometry"
              args={[space(0.75), space(0.1), space(0.75)]}
              rotation={[space(0.95), space(0), space(0)]}
            />
            <animated.meshStandardMaterial
              attach="material"
              emissiveIntensity={springs.lightEmission}
              side={THREE.DoubleSide}
              emissive={"#FFB76B"}
              color={"#22bad6"}
              castShadow
            />
          </mesh>
          <mesh position={[-space(2.4), space(2.75), space(4.725)]}>
            <boxGeometry
              attach="geometry"
              args={[space(0.65), space(0.1), space(0.65)]}
              rotation={[space(0.95), space(0), space(0)]}
            />
            <animated.meshStandardMaterial
              attach="material"
              emissiveIntensity={springs.lightEmission}
              side={THREE.DoubleSide}
              emissive={"#FFB76B"}
              color={"#22bad6"}
            />
          </mesh>
        </animated.group>
        <ObjectImproter
          src={`/assets/obj/casa.gltf`}
          position={[0, 0, 0]}
          rotation={[0, -space(2.5), 0]}
        />
      </group>

      <group
        name="wooden boat at the peer"
        position={[0, space(0.19), space(10.1)]}
      >
        <ObjectImproter
          src={`/assets/obj/wood_boat.gltf`}
          scale={[...vec3(space(2))]}
          rotation={[0, space(2), 0]}
          animate={({ meshRef, delta, clock }) => {
            meshRef.current.position.y = Math.sin(
              new THREE.Vector3(15).length(0.9) / 8 +
                performance.now() * -0.0009
            );
          }}
        />
      </group>
    </group>
  );
};

export default Island;
