import React, { useRef } from "react";
import { useFrame } from "@react-three/fiber";
import { DoubleSide } from "three";
import FormatTexture from "../hooks/FormatTexture";
const Cloud = ({ args = [500, 500], ...props }) => {
  const texture = FormatTexture("assets/img/smoke-1.png");
  const cloudRef = useRef();
  const speed = Math.random() * 0.0009;
  useFrame(({ clock }, delta) => {
    // if(!!cloudRef?.current)
    // return;
    cloudRef.current.position.z -=Math.sin(0.0002 * performance.now() * speed);
    cloudRef.current.position.x -= Math.sin(0.0002 * performance.now() * speed);
  });
  // console.log({cloudRef})
  return (
    <mesh ref={cloudRef} transparent={true} {...props}>
      <planeGeometry
        attach="geometry"
        args={args}
        receiveShadow
        transparent={true}
      />
      <meshLambertMaterial
        attach="material"
        map={texture}
        opacity={0.6}
        transparent={true}
        // Feed the shaders as strings
        side={DoubleSide}
      />
    </mesh>
  );
};
export default Cloud;
