import React, { Suspense } from "react";
import {
  OrbitControls,
  Loader,
  PerspectiveCamera,
} from "@react-three/drei";
import { Canvas } from "@react-three/fiber";
import { space, transitionViewIfSupported } from "../utils";
import {
  EffectComposer,
  DepthOfField,
  Bloom,
  Noise,
  Vignette,
} from "@react-three/postprocessing";
import { useNavigate } from "react-router-dom";
import SunRise from "../three/SurRise";
import Ocean from "../three/Ocean";
import Track from "../three/Track";
import Clouds from "../three/Clouds";
import Island from "../three/Island";
import CreateText from "../three/CreateText";
import jsonData from '../json/data.json'
import OceanPlane from "../three/OceanPlane";
import { TextureOptions } from "../hooks";
export default function Intro() {
  const { menu } = jsonData;
  const navigate = useNavigate();
  const url = "assets/sound/night_by_the_river.mp3";
  return (
    <div className="moon flex h-screen w-screen flex-col items-center justify-center">
      <Suspense fallback={<Loader />}>
        <Canvas performance={{ min: 0.5 }} flat linear shadows>
          <PerspectiveCamera
            makeDefault
            fov={75}
            far={1000}
            near={0.01}
            position={[0, space(5), space(15)]}
            aspect={window.innerWidth / window.innerHeight}
          />
          <ambientLight color="white" intensity={0.25} />
          <directionalLight
            intensity={0.5}
            position={[-space(2.4), space(3), -space(4.73)]}
            color="#ffb76b"
            castShadow
          />
          <SunRise turbidity={0.1} />
          <Clouds />
          <Island />
          <Track url={url} />
           {/* <Ocean
            position={[0, space(0.1), 0]}
            args={[500, 500, 90, 90]}
            rotation={[space(-0.5), 0, 0]}
            wave={{ height: 8, speed: -0.0009, swell: 0.9, offSetTime: 12 }}
            sunColor={"wheat"}
            // waterColor={"rgb(30,175,255)"}
            waterColor={"rgb(75,205,255)"}
            alpha={1}
            map="assets/img/water_normal_cell.jpg"
            castShadow
          />*/}
         <OceanPlane
            position={[0, space(0.1), 0]}
            args={[500, 500, 90, 90]}
            rotation={[space(-0.5), 0, 0]}
            wave={{ height: 8, speed: -0.0009, swell: 0.9, offSetTime: 12 }}
            map="assets/img/water_normal_cell.jpg"
            {...TextureOptions("textures/toonWater")}
            castShadow
          /> 
          <group
           
           position={[space(3), space(10), space(-15)]}
            onClick={() => {
              transitionViewIfSupported(navigate(menu[1].route));
            }}
          >
            <CreateText
              textstring="<-Back"
              fontSize={10}
              fontHeight={0.3}
              position={[0,0,0]}
              hover="#ff99cc"
            />
          </group>
          <OrbitControls
            minPolarAngle={space(0.24)}
            maxPolarAngle={space(0.4)}
            maxAzimuthAngle={space(0.5)}
            minAzimuthAngle={space(-0.5)}
            maxDistance={space(20)}
            minDistance={space(12)}
            enablePan={false}
          />
          <EffectComposer>
            <DepthOfField
              focusDistance={0}
              focalLength={300}
              bokehScale={7.5}
              height={1}
            />
            <Bloom luminanceThreshold={40} luminanceSmoothing={9} height={30} />
            <Noise opacity={0.05} />
            <Vignette eskil={true} offset={0.0} darkness={1.1} />
          </EffectComposer>
        </Canvas>
      </Suspense>
    </div>
  );
}
